import Vue from 'vue'
import VueRouter from 'vue-router'

/**
 * 路由
 */ 
import Restaurant from '@/views/restaurant/restaurant.vue'
    import restaAdd from '@/views/restaurant/subordinate/resta-add/restaAdd.vue'
    import restaEdit from '@/views/restaurant/subordinate/resta-edit/restaEdit.vue'


/**
 * 错误路由
 * 
 * 404 找不到页面
 */ 
import Fhf from '@/error/404' 

Vue.use(VueRouter)

const routes = [
    {
        path: '/restaurant',
        name: '餐饮管理',
        component: Restaurant
    },
    {
        path: '/restaAdd',
        name: '新建餐厅',
        component: restaAdd
    },
    {
        path: '/restaEdit',
        name: '修改餐厅',
        component: restaEdit
    },
    {
        path: '/404',
        name: '404',
        component: Fhf
    },


    // 重定向
    {
        path: '/',
        redirect: '/restaurant'
    }
]


// export default new Router({

//     //路由模式：hash(默认)，history模式
    
//         mode: 'history',
    
//     router:{
        
//         name: '餐饮管理',
//         router: '/'
//     }
// })

export default new VueRouter({
    //路由模式：hash(默认)，history模式

    mode: 'history',
    routes
})
